import {IconCategory} from '@here/hds-iconlibrary';
import React, {useEffect, useImperativeHandle} from 'react';
import {
  Input,
  InputType,
  PasswordVisibility,
  InputSize,
  InputChangePayload,
  InputInputPayload,
  InputActionPayload,
} from '@here/hds-input';

// eslint-disable-next-line no-new
new Input();

export interface HDSInputProperties
  extends Omit<
    React.HTMLAttributes<HTMLInputElement>,
    'onAction' | 'onChange' | 'onBlur' | 'onFocus' | 'onKeyDown' | 'onKeyUp' | 'onInput'
  > {
  value?: string;
  type?: InputType;
  passwordVisibility?: PasswordVisibility;
  id?: string;
  name?: string;
  placeholder?: string;
  label?: string;
  secondaryLabel?: string;
  icon?: string;
  iconCategory?: IconCategory;
  size?: InputSize;
  disabled?: boolean;
  readonly?: boolean;
  required?: boolean;
  autofocus?: boolean;
  minlength?: number;
  maxlength?: number;
  min?: string | number;
  max?: string | number;
  step?: number;
  charCounter?: boolean;
  error?: boolean;
  errorText?: string;
  ariaLabelledby?: string;
  ariaLabel?: string;
  pattern?: string;
  autocomplete?: string;
  onAction?: (e: CustomEvent<InputActionPayload>) => void;
  onChange?: (e: CustomEvent<InputChangePayload>) => void;
  onBlur?: (e: CustomEvent<Input>) => void;
  onFocus?: (e: CustomEvent<Input>) => void;
  onKeyDown?: (e: KeyboardEvent) => void;
  onKeyUp?: (e: KeyboardEvent) => void;
  onInput?: (e: CustomEvent<InputInputPayload>) => void;
}

const HDSInput = React.forwardRef((props: HDSInputProperties, ref) => {
  const {
    value,
    type,
    id,
    name,
    placeholder,
    label,
    secondaryLabel,
    icon,
    iconCategory,
    size,
    disabled,
    readonly,
    required,
    autofocus,
    passwordVisibility,
    minlength,
    maxlength,
    min,
    max,
    pattern,
    charCounter,
    error,
    errorText,
    onAction,
    onChange,
    onBlur,
    onFocus,
    onKeyDown,
    onKeyUp,
    onInput,
    className,
    ariaLabelledby,
    ariaLabel,
    step,
    autocomplete,
    ...rest
  } = props;
  const innerRef = React.useRef<HTMLElement>(null);
  useImperativeHandle(ref, () => innerRef.current);

  const _handleAction = (e: CustomEvent<InputActionPayload>) => {
    if (e.target !== innerRef.current) return;
    onAction && onAction(e);
  };

  const _handleChange = (e: CustomEvent<InputChangePayload>) => {
    if (e.target !== innerRef.current) return;
    onChange && onChange(e);
  };
  const _handleInput = (e: CustomEvent<InputInputPayload>) => {
    if (e.target !== innerRef.current) return;
    onInput && onInput(e);
  };
  const _handleBlur = (e: CustomEvent<Input>) => {
    if (e.target !== innerRef.current) return;
    onBlur && onBlur(e);
  };
  const _handleFocus = (e: CustomEvent<Input>) => {
    if (e.target !== innerRef.current) return;
    onFocus && onFocus(e);
  };
  const _handleKeyDown = (e: KeyboardEvent) => {
    if (e.target !== innerRef.current) return;
    onKeyDown && onKeyDown(e);
  };
  const _handleKeyUp = (e: KeyboardEvent) => {
    if (e.target !== innerRef.current) return;
    onKeyUp && onKeyUp(e);
  };

  useEffect(() => {
    if (onAction && innerRef.current) {
      innerRef.current.addEventListener('action', _handleAction as EventListener);
    }
    return () => innerRef.current?.removeEventListener('action', _handleAction as EventListener);
  }, [onAction, innerRef]);

  useEffect(() => {
    if (onChange && innerRef.current) {
      innerRef.current.addEventListener('change', _handleChange as EventListener);
    }
    return () => innerRef.current?.removeEventListener('change', _handleChange as EventListener);
  }, [onChange, innerRef]);

  useEffect(() => {
    if (onInput && innerRef.current) {
      innerRef.current.addEventListener('input', _handleInput as EventListener);
    }
    return () => innerRef.current?.removeEventListener('input', _handleInput as EventListener);
  }, [onInput, innerRef]);

  useEffect(() => {
    if (onBlur && innerRef.current) {
      innerRef.current.addEventListener('blur', _handleBlur as EventListener);
    }
    return () => innerRef.current?.removeEventListener('blur', _handleBlur as EventListener);
  }, [onBlur, innerRef]);

  useEffect(() => {
    if (onFocus && innerRef.current) {
      innerRef.current.addEventListener('focus', _handleFocus as EventListener);
    }
    return () => innerRef.current?.removeEventListener('focus', _handleFocus as EventListener);
  }, [onFocus, innerRef]);

  useEffect(() => {
    if (onKeyDown && innerRef.current) {
      innerRef.current.addEventListener('keydown', _handleKeyDown as EventListener);
    }
    return () => innerRef.current?.removeEventListener('keydown', _handleKeyDown as EventListener);
  }, [onKeyDown, innerRef]);

  useEffect(() => {
    if (onKeyUp && innerRef.current) {
      innerRef.current.addEventListener('keyup', _handleKeyUp as EventListener);
    }
    return () => innerRef.current?.removeEventListener('keyup', _handleKeyUp as EventListener);
  }, [onKeyUp, innerRef]);
  return (
    <hds-input
      value={value}
      type={type}
      id={id}
      name={name}
      placeholder={placeholder}
      label={label}
      {...(secondaryLabel && {'secondary-label': secondaryLabel})}
      {...(passwordVisibility && {'password-visibility': passwordVisibility})}
      icon={icon}
      {...(iconCategory && {'icon-category': iconCategory})}
      size={size}
      {...(disabled && {disabled: true})}
      {...(readonly && {readonly: true})}
      {...(required && {required: true})}
      {...(autofocus && {autofocus: true})}
      {...(className && {class: className})}
      minlength={minlength}
      maxlength={maxlength}
      min={min}
      max={max}
      step={step}
      pattern={pattern}
      {...(charCounter && {'char-counter': charCounter})}
      {...(error && {error: true})}
      {...(errorText && {'error-text': errorText})}
      {...(ariaLabelledby && {'aria-labelledby': ariaLabelledby})}
      {...(ariaLabel && {'aria-label': ariaLabel})}
      {...(autocomplete && {autocomplete: autocomplete})}
      ref={innerRef}
      {...rest}
    ></hds-input>
  );
});

HDSInput.defaultProps = {
  type: 'text',
  id: '',
  name: undefined,
  placeholder: undefined,
  label: undefined,
  secondaryLabel: undefined,
  size: 'medium',
  icon: undefined,
  pattern: undefined,
  iconCategory: 'core-ui',
  disabled: false,
  readonly: false,
  required: false,
  autofocus: false,
  minlength: -1,
  maxlength: -1,
  step: 1,
  charCounter: false,
  error: false,
  errorText: undefined,
  ariaLabelledby: undefined,
  ariaLabel: '',
  passwordVisibility: 'masked',
  autocomplete: '',
};

export default HDSInput;
